import React, {FunctionComponent} from 'react';
import getComponent from "../../../../utils/cms/component-resolver";
import ClassNames from './SplashPage.module.scss';
import AppLink from "../../../../util-components/Link/Link";
import {CmsPageContext} from "../../../../types/CmsPageContext";
import PageRoot from "../PageRoot/PageRoot";

interface Props {
    pageContext: {
        fields: {
            items: any[]
        }
    }
}

const SplashPage: FunctionComponent<Props & CmsPageContext> =
    (props) => {
        const fields = props.pageContext.fields;
        const components = fields.items.map(item => {
            const Comp = getComponent(item);
            return Comp;
        });

        return (
            <PageRoot {...props.pageContext.meta} withNavigation={false}>
                <div className={ClassNames.SplashPage}>
                    <div><AppLink to="/">Home (hard for now)</AppLink></div>
                    <h1>Super Fancy Splash Page</h1>
                    <p>No navigation</p>
                    <p>We'll not allow all kinds of content types, online special splash page sections, so we know what to expect and animate</p>
                    {components}
                </div>
            </PageRoot>
        );
    };

export default SplashPage;
