import SplitContainer from "../../components/cms/SplitContainer/SplitContainer";
import Image from "../../components/cms/content-types/Image/Image";
import React from "react";
import RichText from "../../components/cms/content-types/RichText/RichText";
import {isDev} from "../environment/environment";

enum ContentTypes {
    ContainerSplitContent = 1,
    Image,
    RichText,
}

interface IContentFulItem {
    sys: {
        id: string,
        contentType: {
            sys: {
                id: string
            }
        }
    }
}

function getComponent(item: IContentFulItem) {
    const orgTypeId = item.sys.contentType.sys.id;
    const typeId = orgTypeId.charAt(0).toUpperCase() + orgTypeId.substring(1);

    const id = item.sys.id;
    const type = ContentTypes[typeId];

    let Component;
    switch (type) {
        case ContentTypes.ContainerSplitContent:
            Component = SplitContainer;
            break;
        case ContentTypes.Image:
            Component = Image;
            break;
        case ContentTypes.RichText:
            Component = RichText;
            break;
        default:
            Component = () => (<div>TO BE IMPLEMENTED ... unknown from CMS</div>);
            if (isDev()) {
                console.error(`Missing implementation for CMS content type [${orgTypeId}]`);
            }
            break;
    }
    return <Component key={id} data={item} />
}

export default getComponent;
